html, body, #root, .App {
  height: 100%;
}

/* App */
.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

/* Header */
.header {
  text-align: center;
}

/* Cover */
.cover {
  width: 100%;
  height: 100%;
  position: absolute;
}

.cover--hidden {
  display: none;
}

/* Board */
.board {
  width: 318px;
  height: 318px;
  border: 1px solid black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

/* Square */
.square {
  width: 104px;
  height: 104px;
  border: 1px solid black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 80px;
}


